import React, { Fragment } from 'react';
import './style.css';


// https://www.youtube.com/watch?v=KcdBOoK3Pfw
class Carousel extends React.Component {

    constructor(){
        super()
        this.state={
            //counter:1,
            carouselList:[],
            carousalDimention:0  
        }
    }
    counter = 1;
    carouselSlide = React.createRef()
    carouselContainer = React.createRef()
    intervalSlider="";

   componentDidMount(){
    let crimages;
        crimages= this.carouselSlide.current.querySelectorAll('img') 
        const carousalDimention = crimages[0].clientWidth;
        this.carouselSlide.current.style=`transform: translateX(-${( carousalDimention * this.counter )}px)`
        this.setState({...this.state,carousalDimention:carousalDimention,carouselList:crimages});
        
        this.intervalSlider =   setInterval(()=>{
            this.nextHandle();
        },5000)
   }
 
componentWillUnmount(){
clearInterval(this.intervalSlider)
}

   Transitioned =() => {
    if(this.state.carouselList[this.counter].id ==='firstClone') {
        this.counter =   this.state.carouselList.length - this.counter;
        this.carouselSlide.current.style=`transition: none; transform: translateX(-${(this.state.carousalDimention * this.counter)}px)`  

    }
    
    if(this.state.carouselList[this.counter].id ==='lastClone') {
        this.counter =   this.state.carouselList.length - 2;
        this.carouselSlide.current.style=`transition: none; transform: translateX(-${(this.state.carousalDimention * this.counter)}px)`  

    }
   }

     nextHandle=()=>{
        if(this.counter>=this.state.carouselList.length-1) return;
        this.counter++;
        this.carouselSlide.current.style=`transition: transform 0.4s ease-in-out; transform: translateX(-${(this.state.carousalDimention * this.counter)}px)`
    }
     prevHandle= () =>{
        if(this.counter<=0) return;
        this.counter--;
        this.carouselSlide.current.style=`transition: transform 0.4s ease-in-out; transform: translateX(-${(this.state.carousalDimention * this.counter)}px)`
    }


render(){return (
        <Fragment>
           {console.log(this.state.carouselLength)}
           {console.log(this.state.carousalDimention)}
           {console.log(this.counter)}
        <div className="carousel-container" ref={this.carouselContainer} onTransitionEnd={this.Transitioned}>
             <div className="carousel-slide" ref={this.carouselSlide}>
                 <img src={require('../../Asset/Carausal/5.jpg')}  alt="5" id="lastClone"></img>
                 <img src={require('../../Asset/Carausal/1.jpg')}  alt="1"></img>
                 <img src={require('../../Asset/Carausal/2.jpg')}  alt="2"></img>
                 <img src={require('../../Asset/Carausal/3.png')}   alt="3"></img>
                 <img src={require('../../Asset/Carausal/4.jpg')}  alt="4"></img>
                 <img src={require('../../Asset/Carausal/5.webp')}  alt="5"></img>
                 <img src={require('../../Asset/Carausal/1.jpg')}  alt="1" id="firstClone"></img>
                 </div>
        </div>
        <img src={require('../../Asset/Carausal/left.png')} id="prev" onClick={this.prevHandle}/> 
                <img src={require('../../Asset/Carausal/right.png')} id="next" onClick={this.nextHandle}/>      
        </Fragment>
        
    );
}
}

export default Carousel;
